	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Данное упражнение сильно напоминает упражнение из тренажерного зала, а именно - сведение рук в тренажере бабочка. В достаточной мере нагружает мышцы груди, но не сравнится с классическим отжиманиями. Сравнительно простое упражнение, может использоваться как опытными, так и новичками. Однако, необходимо досконально изучить технику выполнения.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Отрегулируйте петли, чтоб оба конца были ровны</li>\n  <li>Возьмитесь за рукояти, пройдите вперед до полного натяжения лямок</li>\n  <li>Ладони обращены от себя, руки согнуты в локтях</li>\n  <li>Отводим руки в стороны, равномерно распределяя вес тела на обе руки, опускаемся до комфортного растяжения грудных мышц</li>\n  <li>Плавным движением возвращаем руки в исходное  в исходное положение</li>\n  <li>Так повторять заданное количество повторений</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Избегайте резких движений.</li>\n  <li>Не закидывайте руки назад (плечевой сустав на излом)</li>\n  <li>Не расслабляйтесь в нижней точке</li>\n  <li>Контроль на каждом из этапов выполнения движения</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/z9Yyw0cYnPQ\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/svedenie-ruk-v-petlyah","efficiency":"5","title":"Сведение рук в петлях петлях","image":"/static/0-15-0.png","image_2":"/static/0-15-1.png","image_3":"","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"3","bodypart":"Грудь","filtertype":"base","type":"Базовое","filterinventory":"trx","inventory":"TRX Петли","filterplace":""}}},"pathContext":{}}

